.events {
  margin: 0;
}

.events__wrapper {
  margin: 0;
  padding: 140px 45px;

  background-color: $color-blue;
}

.events__head {
  display: flex;
  justify-content: space-between;
}

.events__title {
  margin: 0;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;
}

.events__arrow {
  background-color: transparent;
  border: 0;

  cursor: pointer;
}

.events__list {
  margin: 0;

  font-size: $baseFontSize;
  line-height: $baseLineHeght + 3;
  color: $color-darker-gray;
}

.events__item {
  position: relative;

  width: 540px;

  margin-right: 16px;

  img {
    display: block;
  }

  p {
    position: absolute;
    right: 5px;
    bottom: 5px;

    margin: 0;
    padding: 16px 22px;
    max-width: 200px;

    background-color: $white;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
