.projects {
  color: $color-dark-gray;
}

.projects__wrapper {
  margin: 0;
  padding: 60px 45px;

  background: $color-gray-light;
}

.projects__title {
  margin: 0;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-darker-gray;
}

.projects__list {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  font-size: $baseFontSize + 4;
  line-height: $baseLineHeght + 6;

  list-style: none;

  li {
    margin-bottom: 20px;
    margin-right: 30px;

    width: 350px;

    background-color: $white;

    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  img {
    display: block;
  }

  p {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    margin: 0;
    padding: 14px 18px;

    text-align: center;
  }

  span {
    flex-basis: 100%;
    flex-grow: 1;

    font-size: $baseFontSize;
    line-height: $baseLineHeght;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
