.news {
  margin: 0;
}

.news__wrapper {
  margin: 0;
  padding: 90px 45px 140px;
}

.news__head {
  display: flex;
  justify-content: space-between;
}

.news__title {
  margin: 0;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-darker-gray;
}

.news__body {
  display: flex;
  justify-content: space-between;
}

.news__own {
  display: flex;
  flex-basis: 100%;

  .news__link {
    display: block;
  }

  img {
    margin-right: 30px;
  }
}

.news__own-body {
  max-width: 255px;
}

.news__head-link {
  font-size: $baseFontSize - 2;
  line-height: $baseLineHeght + 1;
  letter-spacing: 0.05em;
  color: $color-darker-gray;
}

.news__date {
  font-size: $baseFontSize - 6;
  line-height: $baseLineHeght - 7;
  color: $color-gray;
}

.news__text {
  color: $color-gray;
}

.news__link {
  color: $color-gray;
}

.news__list {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;

  li {
    width: 50%;
  }

  img {
    display: block;
  }

  p {
    margin: 0;
  }

  .news__head-link {
    display: inline-block;

    min-height: 46px;
  }

  .news__text {
    margin: 20px 0;
    padding-right: 16px;
  }
}

.news__item-body {
  display: flex;
  align-items: flex-end;

  .news__date {
    margin-left: 16px;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
